body {
  color: #333;
  background-color: #f9f9f9;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.app {
  text-align: center;
}

header {
  color: #fff;
  text-align: center;
  background-color: #2196f3;
  height: 64px;
  margin: 0;
  box-shadow: 0 4px 4px #0000003d;
}

nav {
  align-items: center;
  gap: 20px;
  height: 64px;
  margin-left: 62px;
  display: flex;
}

nav button {
  cursor: pointer;
  color: #ffffffb3;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 21 / 14 * 100%;
  letter-spacing: .25px;
  background-color: #2196f3;
  border: none;
  height: 64px;
  padding: 23px 18px;
}

nav button.active {
  color: #fff;
  background-color: #1e88e5;
}

.gallery {
  grid-template-columns: repeat(auto-fit, minmax(222px, 1fr));
  gap: 32px;
  margin: 48px 62px;
  padding: 0 0 48px;
  display: grid;
}

.card {
  background-color: #fff;
  flex-direction: column;
  align-items: center;
  width: 225px;
  height: 225px;
  transition: all .3s ease-in-out;
  display: flex;
  position: relative;
  overflow: hidden;
}

.card img {
  object-fit: cover;
  width: 100%;
  height: 225px;
}

.heart-icon {
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  stroke: #f24e1e;
  width: 40px;
  transition: fill .3s;
  position: absolute;
  bottom: 14px;
  right: 14px;
}

.heart-icon:hover {
  fill: #f24e1e;
}

.heart-icon.favorited {
  fill: #ff3a00;
  stroke: #ff3a00;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.06666);
  box-shadow: 0 6px 5px #0000003d, 0 9px 18px #0000002e;
}

.card:hover .heart-icon {
  opacity: 1;
  visibility: visible;
  cursor: pointer;
  transform: scale(1.06666);
}

footer p {
  color: #000;
  letter-spacing: .25px;
  text-align: center;
  text-underline-position: from-font;
  text-decoration-skip-ink: none;
  font-family: Roboto;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
}
